import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'

Vue.use(Vuex)

const state = {
  result: {
    pi: 0,
    one: 0,
    three: 0,
    five: 0
  },
  formItem: {
    gender: '',
    section: '',
    diameter: '',
    mitotic: '',
  },
  auth: false
}

export default new Vuex.Store({
  state,
  mutations,
  actions
})



