import * as types from './types'

const mutations = {
  [types.STORE]: (state, data) => {
    state.result = data.result
    state.formItem = data.formItem
  },
  [types.SUBMIT_AUTH]: (state) => {
    state.auth = true
  }
}

export default mutations



