<template>
  <div id="app" class="components-layout-demo-basic grid-demo">
    <Row type="flex" justify="center" class="row">
      <Col :xs="24" :sm="18">
        <div class="header">
          <div class="logo">
            <img src="./assets/logo.png" alt="" height="135" width="225" ref="logo">
          </div>
          <div class="title" ref="title">
            <div class="title__main">
              <span>Gastrointestinal Stromal Tumor</span>
            </div>
            <div class="title__sub">
              <span>RFS Prediction Model</span>
            </div>
          </div>
        </div>
        <div class="content">
          <transition name="fade" mode="out-in">
            <router-view/>
          </transition>
        </div>
      </Col>
    </Row>
    <div class="footer">Ant Design and Vue @2017 By CheckLee</div>
  </div>
</template>

<script>
  // import 'vue-beauty/package/style/vue-beauty.min.css'
  export default {
    name: 'app',
    mounted: function () {
      if (window.innerWidth > 932) {
        this.$refs.logo.style.marginLeft = (window.innerWidth - 932) * 0.1823 + 'px'
        this.$refs.title.style.marginRight = (window.innerWidth - 932) * 0.1823 + 'px'
      }
    }
  }
</script>

<style scoped>
  #app {
    width: 100%;
    height: 100%;
    text-align: center;
    background-color: #fcfdff;
  }
  .row {
    height: 100%;
  }
  #app .header {
    /*background-color: #fcfdff;*/
    background-color: #fcfdff;
    width: 100%;
    top: 0;
    display: flex;
    border-bottom: 1px solid #dae4eb;
    justify-content: flex-start;
    align-items: flex-end;
    z-index: 1;
  }
  #app .content {
    padding-top: 20px;
    padding-bottom: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  #app .footer {
    position: fixed;
    bottom: 0;
    background-color: #fcfdff;
    left: 12.5%;
    width: 75%;
    height: 40px;
    display: flex;
    border-top: 1px solid #dae4eb;
    justify-content: center;
    align-items: center;
  }
  .logo {
    padding-top: 20px;
    padding-bottom: 20px;
    flex: 0 0 225px;
  }
  .title {
    flex: 1;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 180px;
    text-align: right;
    display: flex;
    flex-direction: column;
  }
  .title__main, .title__sub {
    display: flex;
    align-items: center;
    justify-content: right;
    -webkit-justify-content: flex-end;
    -moz-box-pack: end;
    -webkit--moz-box-pack: end;
    box-pack: end;
    font-size: 44px;
    color: #0D3388
  }
  .title__main {
    flex: 2 1 auto;
  }
  .title__sub {
    flex: 1 1 auto;
  }
  .title__main span{
    font-family: 'Doppio One', sans-serif;
    font-weight: 600;
  }
  .title__sub span{
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
  }
  
  @media screen and (max-width: 1440px) {
    .title__main, .title__sub {
      font-size: 40px;
    }
  }
  @media screen and (max-width: 1280px) {
    .title__main, .title__sub {
      font-size: 36px;
    }
  }
  
  @media screen and (max-width: 768px) {
    #app .footer {
      width: 100%;
      left: 0;
    }
    .logo {
      padding-top: 10px;
      padding-bottom: 10px;
      flex: 0 0 140px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .logo img {
      width: 120px;
      height: 72px;
    }
    .title {
      padding-top: 10px;
      padding-bottom: 10px;
      height: 92px;
      margin-right: 10px;
    }
    .title__main, .title__sub {
      font-size: 16px;
    }
  }
  .fade-enter {
    opacity: 0;
  }
  .fade-enter-active {
    transition: opacity 0.5s;
  }
  .fade-leave {
    opacity: 1;
  }
  .fade-leave-active {
    opacity: 0;
    transition: opacity 0.5s
  }
  
</style>



