<template>
  <div class="result">
    <Row type="flex" justify="center">
      <Col :xs="20">
        <Card class="card">
          <p slot="title" class="title">Outcomes</p>
          <Row class="row">
            <Col span="16" class="pre">Probability of being recurrence-free at 1 year</Col>
            <Col span="8" class="rate">{{ onepro }}%</Col>
          </Row>
          <Row class="row">
            <Col span="16" class="pre">Probability of being recurrence-free at 3 year</Col>
            <Col span="8" class="rate">{{ threepro }}%</Col>
          </Row>
          <Row class="row">
            <Col span="16" class="pre">Probability of being recurrence-free at 5 year</Col>
            <Col span="8" class="rate">{{ fivepro }}%</Col>
          </Row>
        </Card>
        <Button type="primary" @click="back" class="button">Back</Button>
      </Col>
    </Row>
  </div>
</template>

<script>
  export default {
    name: 'ResultMobile',
    data() {
      return {
        msg: ''
      }
    },
    mounted: function () {
      console.log(this.$store.state)
    },
    methods: {
      back() {
        this.$router.go(-1)
      }
    },
    computed: {
      onepro: function () {
        return this.$store.state.result.one
      },
      threepro: function () {
        return this.$store.state.result.three
      },
      fivepro: function () {
        return this.$store.state.result.five
      },
      diameter: function () {
        return this.$store.state.formItem.diameter
      },
      gender: function () {
        return (this.$store.state.formItem.gender === '0.27') ? '男' : '女'
      },
      section: function () {
        return (this.$store.state.formItem.section === '0') ? '胃间质瘤' : '肠间质瘤或E-GIST'
      },
      mitotic: function () {
        return (this.$store.state.formItem.mitotic === '0') ? '核分裂象≤5/50HPFs' :
          (this.$store.state.formItem.mitotic === '1.442') ? '核分裂象6-10/50HPFs' : '核分裂象>10/50 HPFs'
      },
      isFragile: function () {
        return (this.$store.state.formItem.isFragile === '0') ? '未破裂' : '已破裂'
      },
    }
  }
</script>

<style scoped>
  .result {
    height: 100%;
    width: 100%;
  }
  .result p:not(.title) {
    font-size: 16px;
    padding: 20px 10px;
  }
  .result .title {
    font-size: 20px;
    text-align: left;
  }
  .button {
    margin-top: 20px;
  }
  .pre {
    font-size: 16px;
    text-align: left;
  }
  .rate {
    line-height: 48px;
    font-size: 30px;
    font-weight: bold;
    color: #338EB5;
  }
  .row:not(:first-child) {
    padding-top: 20px;
  }
</style>



