<template>
  <div class="hello">
    <Row type="flex" justify="center">
      <Col :xs="22" :sm="16">
        <div class="container">
          <Card class="card">
            <p>
              The recurrence-free survival (RFS) prediction model estimates the risk of recurrence
              and metastasis of primary localized gastrointestinal stromal tumors (GISTs) after radical resection.
              This predictive model is only based on several factors and statistics.
              The risk of recurrence and metastasis may be affected by other characteristics of patients.
              When make medical decision, doctors should not only based on predictive model estimates but also patients’ specific clinical conditions.
            </p>
          </Card>
          <Checkbox v-model="read" class="checkbox">I have read the disclaimer and agreed with these terms.</Checkbox>
          <Button :disabled="!read" class="button" type="primary" @click="next">Continue</Button>
        </div>
      </Col>
    </Row>
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex'
  export default {
    name: 'HelloWorld',
    data () {
      return {
        read: false,
        msg: 'Welcome to Your Vue.js App'
      }
    },
    computed: {
      ...mapState([ 'auth' ])
    },
    methods: {
      ...mapActions([ 'submitAuth' ]),
      next() {
        this.submitAuth()
        this.$router.push('/form')
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .container {
    height: 100%;
    margin: 0 auto;
  }
  .card p {
    font-size: 20px;
    line-height: 25px;
    text-align: left;
  }
  
  .checkbox {
    display: block;
    margin-top: 20px;
  }
  .button {
    margin-top: 20px;
  }
  @media screen and (max-width: 768px){
    .card p {
      font-size: 18px;
      line-height: 22px;
    }
    .checkbox, .button {
      margin-top: 10px;
    }
  }
</style>



