<template>
  <div class="form">
    <Row type="flex" justify="center" align="middle">
      <Col :xs="20" :sm="12">
        <Form class="formItem"  label-position="top" :model="formItem" :rules="rules" ref="form">
          <FormItem label="Sex" prop="gender" class="formItemFont">
            <Select v-model="formItem.gender" placeholder=" ">
              <Option value="0.27">Male</Option>
              <Option value="0">Female</Option>
            </Select>
          </FormItem>
          <FormItem label="Tumor Location" prop="section" class="formItemFont">
            <Select v-model="formItem.section" placeholder=" ">
              <Option value="0">Gastric GIST</Option>
              <Option value="0.35">Non-Gastric GIST</Option>
            </Select>
          </FormItem>
          <FormItem label="Tumor Size(cm)" prop="diameter" class="formItemFont">
            <Input v-model="formItem.diameter"></Input>
          </FormItem>
          <FormItem label="Tumor Mitosis Count" prop="mitotic" class="formItemFont">
            <Select v-model="formItem.mitotic" placeholder=" ">
              <Option value="0">≤ 5/50 HPFs</Option>
              <Option value="1.442">6-10/50 HPFs</Option>
              <Option value="2.026">> 10/50 HPFs</Option>
            </Select>
          </FormItem>
          <FormItem label="Rupture" prop="isFragile" class="formItemFont">
            <Select v-model="formItem.isFragile" placeholder=" ">
              <Option value="0">No Tumor Rupture</Option>
              <Option value="1.259">Tumor Rupture</Option>
            </Select>
          </FormItem>
        </Form>
        <Button type="ghost" @click="reset('form')">Reset</Button>
        <Button type="primary" @click="submit('form')" style="margin-left: 10px">Submit</Button>
      </Col>
      <Col :xs="20" :sm="12">
        <transition name="fade" mode="out-in">
          <Result :result="result" v-show="showRight" :formItem="formItem" class="result"></Result>
        </transition>
      </Col>
    </Row>
    <!--<div class="container" v-show="show">
      <a class="close" @click="close">X</a>
      <Result :result="result" :formItem="formItem" class="result"></Result>
    </div>-->
  </div>
</template>

<script>
  import Result from './Result.vue'
  import * as types from '../store/types'
  export default {
    name: 'form',
    
    data() {
      const numberValidate = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('This field is required'))
        }
        else if (isNaN(value)) {
          callback(new Error('Please enter a number'))
        }
        else {
          callback()
        }
      }
      const checkboxValidate = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('Please select an option'))
        }
        else {
          callback()
        }
      }
      return {
        show: false,
        result: {
          pi: 0,
          one: 0,
          three: 0,
          five: 0
        },
        formItem: {
          gender: '',
          section: '',
          diameter: '',
          mitotic: '',
          isFragile: ''
        },
        rules: {
          gender: [
            { validator: checkboxValidate, trigger: 'change' }
          ],
          section: [
            { validator: checkboxValidate, trigger: 'change' }
          ],
          diameter: [
            { validator: numberValidate, trigger: 'blur' },
            // { type: 'number', message: '格式不正确', trigger: 'blur' }
          ],
          mitotic: [
            { validator: checkboxValidate, trigger: 'change' }
          ],
          isFragile: [
            { validator: checkboxValidate, trigger: 'change' }
          ]
        }
      }
    },
    
    methods: {
      submit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            console.log('success!')
            this.count()
            let tmp = {}
            tmp.result = this.result
            tmp.formItem = this.formItem
            console.log(tmp)
            this.$store.commit(types.STORE, tmp)
            if (this.showPC) {
              this.show = true
            }
            else {
              this.$router.push('/resultMobile')
            }
          }
          else {
            console.log('error!')
          }
        })
      },
      reset(name) {
        this.$refs[name].resetFields()
        this.show = false
      },
      count() {
        this.result.pi = Number(this.formItem.gender) + Number(this.formItem.section) + Number(this.formItem.mitotic) + Number(this.formItem.isFragile) + Number(this.formItem.diameter) * 0.096
        this.result.one = (Math.pow(0.9926, Math.exp(this.result.pi)) * 100).toFixed(1)
        this.result.three = (Math.pow(0.9739, Math.exp(this.result.pi)) * 100).toFixed(1)
        this.result.five = (Math.pow(0.9471, Math.exp(this.result.pi)) * 100).toFixed(1)
      },
      close() {
        this.show = false
      }
    },
    
    computed: {
      showPC: function () {
        return (window.innerWidth >= 769) ? true : false
      },
      showRight: function () {
        return this.showPC && this.show
      }
    },
    
    components: { Result }
  }
</script>

<style scoped>
  .form {
    position: relative;
    width: 100%;
  }
  .formItemFont {
    font-size: 20px !important;
  }
  .formItemFont {
    text-align: left !important;
  }
  .ivu-select-selected-value, .ivu-select-item {
    text-align: center !important;
  }
  @media screen and (min-width: 769px) {
    .formItem {
      max-width: 300px;
    }
    .close {
      right: 70px;
    }
    .formItemFont {
      font-size: 20px !important;
    }
  }
  .formItem, .result {
    max-width: 350px;
    margin: 0 auto;
  }
  .container {
    position: relative;
    max-width: 300px;
    margin: 0 auto;
    padding-top: 20px;
  }
  .close {
    font-size: 20px;
    color: #000;
    position: absolute;
    z-index: 999;
    top: 30px;
    right: 40px;
  }

  @media screen and (max-width: 768px) {
    .container {
      position: absolute;
      top: 0;
      padding: 0;
      width: 100%;
      left: 50%;
      transform: translate(-50%)
    }
    .result {
      height: 80%;
    }
    .close {
      top: 10px
    }
  }
  .fade-enter {
    opacity: 0;
  }
  .fade-enter-active {
    transition: opacity 0.5s;
  }
  .fade-leave {
    opacity: 1;
  }
  .fade-leave-active {
    opacity: 0;
    transition: opacity 0.5s
  }
</style>



