<template>
  <div class="result">
    <Card class="card">
      <p slot="title" class="title">Outcomes</p>
      <Row class="row">
        <Col span="16" class="pre">Probability of being recurrence-free at 1 year</Col>
        <Col span="8" class="rate">{{ onepro }}%</Col>
      </Row>
      <Row class="row">
        <Col span="16" class="pre">Probability of being recurrence-free at 3 year</Col>
        <Col span="8" class="rate">{{ threepro }}%</Col>
      </Row>
      <Row class="row">
        <Col span="16" class="pre">Probability of being recurrence-free at 5 year</Col>
        <Col span="8" class="rate">{{ fivepro }}%</Col>
      </Row>
    </Card>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        msg: '00'
      }
    },
    props: {
      result: Object,
      formItem: Object,
    },
    computed: {
      onepro: function () {
        return this.result.one
      },
      threepro: function () {
        return this.result.three
      },
      fivepro: function () {
        return this.result.five
      },
      gender: function () {
        return (this.formItem.gender === '0.27') ? '男' : '女'
      },
      section: function () {
        return (this.formItem.section === '0') ? '胃间质瘤' : '肠间质瘤或E-GIST'
      },
      mitotic: function () {
        return (this.formItem.mitotic === '0') ? '核分裂象≤5/50HPFs' :
          (this.formItem.mitotic === '1.442') ? '核分裂象6-10/50HPFs' : '核分裂象>10/50 HPFs'
      },
      isFragile: function () {
        return (this.formItem.isFragile === '0') ? '未破裂' : '已破裂'
      },
      show: function () {
        return (window.innerWidth < 768) ? true : false
      }
    },
    
  }
</script>

<style scoped>
  .content {
    text-align: left;
    padding-left: 52px;
  }
  .result {
    height: 100%;
    width: 100%;
  }
  .result p:not(.title) {
    font-size: 16px;
    padding: 20px 10px;
  }
  .result .title {
    font-size: 20px;
    text-align: left;
  }
  .pre {
    font-size: 16px;
    text-align: left;
  }
  .rate {
    line-height: 48px;
    font-size: 30px;
    font-weight: bold;
    color: #338EB5;
  }
  .row:not(:first-child) {
    padding-top: 20px;
  }
</style>



